.player{
    width: 100%;
    // text-align: center;
    p{
        margin-top: 24px;
        text-align: center;
        font-size: 24px;
        font-weight: 700;
    }
    .player-ul{
        width: 80%;
        margin: 0 auto;
        display: flex;
        column-gap: 12px;
        li{
            flex: 1;
            text-align: center;
            h3{
                font-size: 18px;
                font-weight: 700;
            }
            form{
                display: flex;
                justify-content: center;
                gap: 12px;
                input{
                    &:nth-child(1){
                        outline-color: #1890ff;
                        border: none;
                        border: 1px solid #767676;
                    }
                }
            }
            &.display-P1{
                background-color: #dfdfdf;
                display: flex;
                justify-content: space-between;
                align-items: center;
                column-gap: 8px;
                padding: 0 15px;
                figure{
                    display: flex;
                    align-items: center;
                    column-gap: 16px;
                    height: 100%;
                    margin: 0;
                    flex: 1;
                    img{
                        width: 50px;
                        height: 50px;
                        margin-left: 16px;
                        border-radius: 10px;
                    }
                    figcaption{
                        font-size: 18px;
                        font-weight: 700;
                        color: #1890ff;
                    }
                }
                .btn-close{
                    width: 25px;
                    height: 25px;
                    border: none;
                    border-radius: 50%;
                    background-color: #f5222d;
                    color: #fff;
                    font-weight: 700;
                    cursor: pointer;
                }
            }
            &.display-P2{
                background-color: #dfdfdf;
                display: flex;
                justify-content: space-between;
                align-items: center;
                column-gap: 8px;
                padding: 0 15px;
                figure{
                    display: flex;
                    align-items: center;
                    column-gap: 16px;
                    height: 100%;
                    margin: 0;
                    flex: 1;
                    img{
                        width: 55px;
                        height: 55px;
                        margin-left: 16px;
                        border-radius: 10px;
                    }
                    figcaption{
                        font-size: 18px;
                        font-weight: 700;
                        color: #1890ff;
                    }
                }
                .btn-close{
                    width: 25px;
                    height: 25px;
                    border: none;
                    border-radius: 50%;
                    background-color: #f5222d;
                    color: #fff;
                    font-weight: 700;
                    cursor: pointer;
                }
            }
        }
    }
    .btn-battle{
        display: block;
        margin-top: 36px;
        background-color: #efefef;
        border: 1px solid black;
        text-align: center;
        color: black;
    }
    .btn-battle:hover{
        color: #1890ff;
        cursor: pointer;
    }
}
@media screen and (max-width:992px) {
    .player{
        p{
            font-size: 18px;
        }
        .player-ul{
            width: 100%;
            flex-wrap: wrap;
            row-gap: 0.8rem;
        }
    }
}