.instruction{
    width: 100%;
    p{
        text-align: center;
        font-size: 24px;
        font-weight: 700;
    }
    .instruction-ul{
        width: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
        figure{
            margin: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            figcaption{
                font-size: 18px;
                font-weight: 700;
                padding: 12px 0;
                
            }
        }
    }
}
@media screen and (max-width:992px) {
    .instruction{
        p{
            font-size: 18px;
        }
        .instruction-ul{
            figcaption{
                font-size: 0.6rem !important;
            }
        }
    }
}